// src/pages/Users/UsersList.jsx
import React, { useCallback, useEffect, useState } from 'react';
import { usersApi } from '../../services/api';
import {
  Button,
  Input,
  Checkbox,
  OverlayModal,
  NoticeModal,
  ConfirmModal,
  Badge,
  IconButton,
  LineNumberTextarea,
  EditIcon,
  DeleteIcon,
  PageHeader,
  ErrorPage,
  Spinner,
  EmptyStateCard
} from '../../components/common';
import { useIsMobile } from '../../hooks';
import { parseApiError } from '../../utils';
import { pageStyles } from '../../styles/pageStyles';
import { colors, spacing, borderRadius, shadows, typography } from '../../styles/theme';

export default function UsersList() {
  const isMobile = useIsMobile();

  // --- State: Data ---
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // --- State: Pagination ---
  const [page, setPage] = useState(1);
  const [hasNext, setHasNext] = useState(false);
  const [hasPrev, setHasPrev] = useState(false);

  // --- State: Modals ---
  const [editOpen, setEditOpen] = useState(false);
  const [editUser, setEditUser] = useState(null);
  const [extrasText, setExtrasText] = useState('{}');
  const [saving, setSaving] = useState(false);
  const [modalError, setModalError] = useState(null);

  const [deleteOpen, setDeleteOpen] = useState(false);
  const [deleteUser, setDeleteUser] = useState(null);
  const [deleting, setDeleting] = useState(false);

  const [cleanRegistrationsOpen, setCleanRegistrationsOpen] = useState(false);
  const [cleanPaymentsOpen, setCleanPaymentsOpen] = useState(false);
  const [cleaning, setCleaning] = useState(false);

  const [notice, setNotice] = useState({ open: false, type: 'success', text: '' });

  // --- Loading Logic ---
  const loadUsers = useCallback(async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await usersApi.list({
        params: {
          page: page
        }
      });

      const results = response.results || response || [];
      setUsers(results);

      setHasNext(!!response.next);
      setHasPrev(!!response.previous);
    } catch (e) {
      console.error(e);
      if (e.response?.status === 404 && page > 1) {
        setPage(1);
      } else {
        setError(parseApiError(e));
      }
    } finally {
      setLoading(false);
    }
  }, [page]);

  useEffect(() => {
    loadUsers();
  }, [loadUsers]);

  // --- Handlers: Pagination ---
  const handleNext = () => {
    if (hasNext) setPage(p => p + 1);
  };

  const handlePrev = () => {
    if (hasPrev) setPage(p => Math.max(1, p - 1));
  };

  // --- Handlers: Edit ---
  const openEdit = (user) => {
    setModalError(null);
    setEditUser({ ...user });
    const extras = user?.extras && typeof user.extras === 'object' ? user.extras : {};
    setExtrasText(JSON.stringify(extras, null, 2));
    setEditOpen(true);
  };

  const handleSaveUser = async (e) => {
    e.preventDefault();
    if (!editUser) return;
    setModalError(null);

    let extrasObj = {};
    try {
      extrasObj = extrasText.trim() ? JSON.parse(extrasText) : {};
    } catch (parseErr) {
      setModalError(` JSON: ${parseErr.message}`);
      return;
    }

    setSaving(true);
    try {
      const payload = {
        username: editUser.username,
        email: editUser.email,
        phone: editUser.phone,
        is_registered: !!editUser.is_registered,
        paid: !!editUser.paid,
        extras: extrasObj,
      };

      await usersApi.update(editUser.id, payload);
      setEditOpen(false);
      setNotice({ open: true, type: 'success', text: ` ${editUser.username} ` });
      loadUsers();
    } catch (err) {
      setModalError(parseApiError(err));
    } finally {
      setSaving(false);
    }
  };

  const formatExtras = () => {
    try {
      const obj = extrasText.trim() ? JSON.parse(extrasText) : {};
      setExtrasText(JSON.stringify(obj, null, 2));
      setModalError(null);
    } catch (e) {
      setModalError(` JSON: ${e.message}`);
    }
  };

  // --- Handlers: Delete ---
  const openDelete = (user) => {
    setDeleteUser(user);
    setDeleteOpen(true);
  };

  const handleConfirmDelete = async () => {
    if (!deleteUser) return;
    setDeleting(true);
    try {
      await usersApi.remove(deleteUser.id);
      setDeleteOpen(false);
      setNotice({ open: true, type: 'success', text: ' ' });

      if (editOpen && editUser?.id === deleteUser.id) {
        setEditOpen(false);
      }

      loadUsers();
    } catch (err) {
      setNotice({ open: true, type: 'error', text: parseApiError(err) });
    } finally {
      setDeleting(false);
    }
  };

  // --- Handlers: Batch Actions ---
  const handleBatchAction = async (actionType) => {
    setCleaning(true);
    try {
      if (actionType === 'registrations') {
        await usersApi.cleanRegistrations();
        setCleanRegistrationsOpen(false);
      } else {
        await usersApi.cleanPayments();
        setCleanPaymentsOpen(false);
      }
      setNotice({ open: true, type: 'success', text: '  ' });
      loadUsers();
    } catch (err) {
      setNotice({ open: true, type: 'error', text: parseApiError(err) });
    } finally {
      setCleaning(false);
    }
  };

  if (error) return <ErrorPage code="500" message={error} />;

  const gridStyle = {
    display: 'grid',
    gridTemplateColumns: isMobile ? '1fr' : 'repeat(auto-fill, minmax(340px, 1fr))',
    gap: spacing.md,
    maxWidth: '100%',
    width: '100%',
  };

  return (
    <div style={pageStyles.page}>
      <div style={pageStyles.container}>
        <PageHeader
          title=""
          subtitle="  "
          isMobile={isMobile}
          actions={
            <div style={{ display: 'flex', gap: spacing.sm, flexDirection: isMobile ? 'column' : 'row', width: isMobile ? '100%' : 'auto' }}>
              <Button
                variant="primary"
                onClick={() => setCleanRegistrationsOpen(true)}
                disabled={loading}
                fullWidth={isMobile}
              >
                 
              </Button>
              <Button
                variant="primary"
                onClick={() => setCleanPaymentsOpen(true)}
                disabled={loading}
                fullWidth={isMobile}
              >
                 
              </Button>
            </div>
          }
        />

        {loading ? (
          <div style={styles.center}>
            <Spinner size={40} />
          </div>
        ) : users.length === 0 ? (
          <EmptyStateCard
            title=" "
            description="     ."
            isMobile={isMobile}
          />
        ) : (
          <>
            <div style={gridStyle}>
              {users.map((user) => (
                <UserCard
                  key={user.id}
                  user={user}
                  isMobile={isMobile}
                  onEdit={() => openEdit(user)}
                />
              ))}
            </div>

            <div style={styles.pagination}>
              <Button
                variant="secondary"
                disabled={!hasPrev || loading}
                onClick={handlePrev}
              >
                 
              </Button>

              <div style={styles.pageNumber}>
                {page}
              </div>

              <Button
                variant="secondary"
                disabled={!hasNext || loading}
                onClick={handleNext}
              >
                 
              </Button>
            </div>
          </>
        )}
      </div>

      <OverlayModal open={editOpen} title="" onClose={() => setEditOpen(false)}>
        <form onSubmit={handleSaveUser} style={{ display: 'flex', flexDirection: 'column', gap: spacing.lg }}>
          <Input
            label=" "
            required
            value={editUser?.username || ''}
            onChange={(e) => setEditUser({ ...editUser, username: e.target.value })}
          />

          <div style={{ display: 'grid', gridTemplateColumns: isMobile ? '1fr' : '1fr 1fr', gap: spacing.md }}>
            <Input
              label="Email"
              type="email"
              value={editUser?.email || ''}
              onChange={(e) => setEditUser({ ...editUser, email: e.target.value })}
            />
            <Input
              label=""
              value={editUser?.phone || ''}
              onChange={(e) => setEditUser({ ...editUser, phone: e.target.value })}
            />
          </div>

          <div style={{ display: 'flex', flexDirection: 'column', gap: spacing.sm }}>
            <Checkbox
              checked={!!editUser?.is_registered}
              onChange={() => setEditUser({ ...editUser, is_registered: !editUser.is_registered })}
              title=""
              description="   "
            />
            <Checkbox
              checked={!!editUser?.paid}
              onChange={() => setEditUser({ ...editUser, paid: !editUser.paid })}
              title=""
              description="  "
            />
          </div>

          <div>
            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 8 }}>
              <label style={styles.label}>.  (JSON)</label>
              <Button
                variant="secondary"
                onClick={formatExtras}
                type="button"
                style={{
                  padding: '4px 10px',
                  height: 'auto',
                  fontSize: 12,
                  fontFamily: typography.fontFamily
                }}
              >
                
              </Button>
            </div>
            <LineNumberTextarea
              value={extrasText}
              onChange={(e) => setExtrasText(e.target.value)}
              rows={6}
              placeholder='{"key": "value"}'
            />
          </div>

          {modalError && <div style={styles.errorBox}>{modalError}</div>}

          <div style={styles.modalActions}>
            <Button variant="secondary" onClick={() => {
              setEditOpen(false);
              openDelete(editUser);
            }} type="button">
              
            </Button>
            <Button variant="primary" type="submit" loading={saving}>
              
            </Button>
          </div>
        </form>
      </OverlayModal>

      <ConfirmModal
        open={deleteOpen}
        title=" "
        message={` ,    ${deleteUser?.username}?`}
        description="   ."
        confirmText=""
        cancelText=""
        loading={deleting}
        onConfirm={handleConfirmDelete}
        onClose={() => setDeleteOpen(false)}
      />

      <ConfirmModal
        open={cleanRegistrationsOpen}
        title=" "
        message="  ?"
        description="       ."
        confirmText=""
        loading={cleaning}
        onConfirm={() => handleBatchAction('registrations')}
        onClose={() => setCleanRegistrationsOpen(false)}
      />

      <ConfirmModal
        open={cleanPaymentsOpen}
        title="  "
        message="       ."
        confirmText=""
        loading={cleaning}
        onConfirm={() => handleBatchAction('payments')}
        onClose={() => setCleanPaymentsOpen(false)}
      />

      <NoticeModal
        open={notice.open}
        type={notice.type}
        text={notice.text}
        onClose={() => setNotice({ ...notice, open: false })}
      />
    </div>
  );
}

function UserCard({ user, isMobile, onEdit }) {
  const [isHovered, setIsHovered] = useState(false);

  return (
    <div
      style={{
        ...styles.card,
        transform: isHovered && !isMobile ? 'translateY(-2px)' : 'none',
        boxShadow: isHovered && !isMobile ? shadows.md : shadows.card,
      }}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      onClick={onEdit}
    >
      <div style={styles.cardHeader}>
        <div style={{marginLeft: spacing.xs}}>
          <svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" fill="currentColor"
               className="bi bi-person-fill-gear" viewBox="0 0 16 16">
            <path
                d="M11 5a3 3 0 1 1-6 0 3 3 0 0 1 6 0m-9 8c0 1 1 1 1 1h5.256A4.5 4.5 0 0 1 8 12.5a4.5 4.5 0 0 1 1.544-3.393Q8.844 9.002 8 9c-5 0-6 3-6 4m9.886-3.54c.18-.613 1.048-.613 1.229 0l.043.148a.64.64 0 0 0 .921.382l.136-.074c.561-.306 1.175.308.87.869l-.075.136a.64.64 0 0 0 .382.92l.149.045c.612.18.612 1.048 0 1.229l-.15.043a.64.64 0 0 0-.38.921l.074.136c.305.561-.309 1.175-.87.87l-.136-.075a.64.64 0 0 0-.92.382l-.045.149c-.18.612-1.048.612-1.229 0l-.043-.15a.64.64 0 0 0-.921-.38l-.136.074c-.561.305-1.175-.309-.87-.87l.075-.136a.64.64 0 0 0-.382-.92l-.148-.045c-.613-.18-.613-1.048 0-1.229l.148-.043a.64.64 0 0 0 .382-.921l-.074-.136c-.306-.561.308-1.175.869-.87l.136.075a.64.64 0 0 0 .92-.382zM14 12.5a1.5 1.5 0 1 0-3 0 1.5 1.5 0 0 0 3 0"/>
          </svg>
        </div>
        <div style={styles.headerInfo}>
          <div style={styles.usernameRow}>
            <span style={styles.username} title={user.username}>@{user.username || ' '}</span>
            {user.is_superuser && <label style={styles.adminLabel} htmlFor=""></label>}
            <label style={styles.userLabel} htmlFor="">,  </label>
              </div>
              </div>
              </div>

              <div style={styles.infoGrid}>
            <InfoRow label="EMAIL" value={user.email} style={{fontWeight: 600}}/>
        <InfoRow label="" value={user.phone} />
      </div>

      <div style={styles.badges}>
        <Badge
          variant={user.is_registered ? 'success' : 'inactive'}
          style={styles.fullWidthBadge}
        >
          {user.is_registered ? '' : ' '}
        </Badge>
        <Badge
          variant={user.paid ? 'warning' : 'inactive'}
          style={styles.fullWidthBadge}
        >
          {user.paid ? '' : ' '}
        </Badge>
      </div>
    </div>
  );
}

const InfoRow = ({ label, value }) => (
  <div style={{ fontSize: 13, display: 'flex', flexDirection: 'column', overflow: 'hidden' }}>
    <span style={{ color: colors.gray400, fontSize: 12, fontWeight: 800 }}>{label}</span>
    <span style={{
      color: value ? colors.text : colors.gray400,
      fontWeight: 600,
      whiteSpace: 'nowrap',
      overflow: 'hidden',
      textOverflow: 'ellipsis',
      fontSize: 14
    }}>
      {value || ''}
    </span>
  </div>
);

/* ---------- STYLES ---------- */

const styles = {
  center: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    minHeight: '40vh',
  },
  pagination: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    marginTop: spacing.xl,
    gap: spacing.md,
    paddingBottom: spacing.xl,
  },
  pageNumber: {
    fontSize: 14,
    fontWeight: 600,
    color: colors.gray500,
  },
  card: {
    background: colors.white,
    borderRadius: borderRadius.card,
    boxShadow: shadows.card,
    padding: spacing.md,
    border: `1px solid ${colors.gray100}`,
    transition: 'all 0.2s ease',
    display: 'flex',
    flexDirection: 'column',
    cursor: 'pointer', //  
    overflow: 'hidden',
    position: 'relative',
  },
  cardHeader: {
    display: 'flex',
    alignItems: 'center',
    gap: spacing.md,
    marginBottom: spacing.md,
    minWidth: 0,
  },
  headerInfo: {
    flex: 1,
    overflow: 'hidden',
    minWidth: 0,
  },
  usernameRow: {
    display: 'flex',
    flexDirection: 'column',
  },
  userLabel: {
    fontWeight: 600,
    fontSize: 12,
    color: colors.gray400
  },

  adminLabel: {
    fontWeight: 600,
    fontSize: 12,
    color: colors.gray500
  },
  username: {
    fontSize: 16,
    fontWeight: 600,
    color: colors.text,
    whiteSpace: 'nowrap',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
    display: 'block',
  },
  infoGrid: {
    display: 'flex',
    flexDirection: 'column',
    gap: spacing.sm,
    marginBottom: spacing.md,
    background: colors.gray50,
    padding: spacing.sm,
    borderRadius: borderRadius.medium,
  },
  badges: {
    display: 'grid', //   
    gridTemplateColumns: '1fr 1fr',
    gap: spacing.xs,
    marginTop: 'auto',
  },
  fullWidthBadge: {
    width: '100%',
    display: 'flex',
    justifyContent: 'center',
    padding: '6px 4px',
    boxSizing: 'border-box'
  },
  label: {
    fontSize: 12,
    fontWeight: 600,
    color: colors.gray500,
    textTransform: 'uppercase',
  },
  modalActions: {
    display: 'flex',
    justifyContent: 'space-between',
    gap: spacing.md,
    marginTop: spacing.md,
  },
  errorBox: {
    background: colors.errorBg,
    color: colors.errorText,
    padding: spacing.md,
    borderRadius: borderRadius.medium,
    fontSize: 13,
  },
};